import { Component } from "react";
import './card.css'

class InstructorCards extends Component {
    constructor(props){
        super(props);
        this.state = {
            certificates : [],
            courseDat : this.props.courseData


        }

    }

    componentDidMount(props){
        const mounted_data = []



        for (const data of this.state.courseDat){
            console.log(data.sections[0].has_started)
            const payload = {
                "course_name" : data.sections[0].course_display_name,
                "enrollment_count" : data.sections[0].enrollment_count.total
            }
            mounted_data.push(payload)
            console.log(data)


        }
        this.setState(()=>{ return {
            certificates : mounted_data
        }

        })
    }


     render() {
         console.log(this.state.certificates)
         const {courseData} = this.props
         return (
             <div >

             <div className="cardd demo w-25 " style={{width: "18rem"}}>
             {/*    <div className="cardd w-10">*/}
                 <div className="card-body">
                     <h5 className="card-title">Total Courses</h5>
                     <p className="card-text">{courseData.length} </p>
                 </div>
                 </div>
                 <h2> </h2>
                 <caption><h5> Courses: </h5></caption>
                 <div className="card-group">

                 {
                    this.state.certificates.map(data=>{

                        return(

                            <div className="cardd">
                           <div className="card-body">
                               <h5 className="card-title">{data.course_name}</h5>
                           <p className="card-text">Enrollment Count: {data.enrollment_count} </p>
                            </div>
                            </div>

                        )
                    })
                }
                 </div>
             </div>

         )
     }



}





export default InstructorCards;