import Highcharts from 'highcharts';
import { Component } from 'react';

class InstructorBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            series : [],
            allCourseData : props.courseData
        }
    }

    componentDidMount() {
        const [enrollment_counts, course_names] = [[],[]]
        for (let data of this.state.allCourseData){
            // console.log()
            const course_name = data.sections[0].course_display_name
            const enrollment_count = data.sections[0].enrollment_count.total


            enrollment_counts.push(enrollment_count)
            course_names.push(course_name)
        };

        this.setState(
            ()=>{ return {
                series: [{
                    name: 'Enrollment',
                    data:enrollment_counts,
                    color: '#3498db',
                    index: 1
                },] //{
                //     name: 'Not Completed',
                //     data: not_completed,
                //     color: '#9b59b6',
                //     index: 0
                // }]
            }},
            // ()=>{console.log(this.state.series)}
            ()=>{console.log(this.state.series);this.highChartsRender(course_names);}
            )
    }
    highChartsRender = (coursenames)=> {
        //console.log('highChartsRender called')
        Highcharts.chart({
            chart: {
                type: 'column',
                renderTo: 'leaner-dashboard'
            },
            title: {
                text: 'Enrollment Counts Status'
            },
            xAxis: {
                categories: coursenames
            },
            yAxis: {
                min: 0,
                max: 100,
                title: {
                    text: ''
                }
            },
            credits: {
                enabled: false
            },
            legend: {
                reversed: true
            },
            plotOptions: {
                series: {
                    stacking: 'normal'
                },
                column: {
                    dataLabels: {
                      enabled: true,
                      useHTML: true,
                      formatter: function() {
                        return '<div style="text-align:top;"><div>' + this.y + '' + '</div></div>';
                      }
                    }
                },
            },
            tooltip: {
                formatter: function() {
                    return this.x + '<br>' + 'progress: ' + this.y + '';
                }
            },
            series: this.state.series
        });
    };

    render() {
        console.log('Render called')
        return (
         <div id="leaner-dashboard">
         </div>
        );
    }
}

export default InstructorBar;